<template>

  <div class="container">
     <br><br>
          <form>
 <div class="form-group col-8">
    <label for="exampleFormControlInput1" ><h5>Enter your name:</h5></label>
    <input type="text" class="form-control" id="name" name="name" placeholder="Name" v-model="name" required>
  </div>
        <div class="form-group col-8">
    <label for="exampleFormControlTextarea1"><h5>Enter your message:</h5></label>
 <textarea class="form-control" id="message" rows="4" name="message" placeholder="Message" v-model="message" required></textarea>
 <br>
   <button type="submit" @click="sub" class="btn btn-primary">Submit</button> &nbsp;
    <button type="reset" class="btn btn-primary">Reset</button>
 
  </div>

</form>
     <br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br><br>
  </div>
</template>

<script>
export default {

data(){
    return{
        name:'',
        message:''
    }
},
methods:{

    sub(){

       let n= document.getElementById("name").value;
       let m= document.getElementById("message").value;
         localStorage.setItem("name",n);
         localStorage.setItem("message",m);

let nam= localStorage.getItem("name");
let mas= localStorage.getItem("message");
console.log(nam);
console.log(mas);

  alert("Thank you "+ nam +" for submitting your message. Ritesh will respond you soon.");
    }

   

}
}
</script>

